
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Header from './components/Header';

import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import Shop from './components/Shop';
//import PicDetail from './components/PicDetail';
import Footer from './components/Footer';
import Home from './components/Home';
import Contatti from './components/Contatti';

function App() {
    return (
        <Router basename={process.env.PUBLIC_URL}>
            <Header />

            <Switch>
                <Route exact path='/' render={() => <Home />} />
                <Route exact path='/artists' render={() => <Shop />} />
                <Route exact path='/contacts' render={() => <Contatti />} />
                {/*<Route exact path='/about' render={() => <h1>CIAOO</h1>} />*/}

                {/*<Route exact path='/paintings/:id' render={() => <PicDetail />} />*/}

                <Route><Redirect to='/'/></Route>
            </Switch>

            <Footer />
        </Router>
    );
}

export default App;
